import { Product } from "../types/product"
import { ProductDTO } from "../types/ProductDTO"


const API_URL = 'http://localhost:8080/admin/products'
//handle get all product
export async function getAllProducts(): Promise<Product[]> {
  const res = await fetch(API_URL, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
    },
    next: { revalidate: 0 }, // Disable cache nếu cần real-time
  })

  if (!res.ok) {
    throw new Error('Lỗi khi tải danh sách sản phẩm')
  }

  return res.json()
}

//handle create product(productService.ts)
export async function createProduct(formData: FormData): Promise<Product> {
    const res = await fetch(API_URL, {
        method: 'POST',
        body: formData,
        next: { revalidate: 0 },
    });

    if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.message || 'Lỗi khi tạo sản phẩm');
    }

    return res.json();
}